import { Button, List, ListItem } from "@chakra-ui/react"
import { Link } from "react-scroll"

function HeaderMenu({ isLargeThan850 }) {
  return (
    <List
      spacing={!isLargeThan850 && 3}
      justifyContent={isLargeThan850 && "space-around"}
      alignItems="center"
      flexGrow={isLargeThan850 && 1}
      display={isLargeThan850 && "flex"}
      fontFamily="Roboto"
    >
      <ListItem>
        <Link to="home" containerId="container">
          <Button
            width="100%"
            colorScheme="blue"
            // bg="#99bbad"
            // color="#000"
            // _hover={{
            //   background: "#99bbad",
            //   color: "#000",
            // }}
          >
            Home
          </Button>
        </Link>
      </ListItem>
      <ListItem>
        <Link to="about" containerId="container">
          <Button width="100%" colorScheme="blue">
            About
          </Button>
        </Link>
      </ListItem>
      <ListItem>
        <Link to="skills" containerId="container">
          <Button width="100%" colorScheme="blue">
            Skills
          </Button>
        </Link>
      </ListItem>
      <ListItem>
        <Link to="contact" containerId="container">
          <Button width="100%" colorScheme="blue">
            Contact
          </Button>
        </Link>
      </ListItem>
    </List>
  )
}

export default HeaderMenu
