import { useState } from "react"
import { Box, Image, useMediaQuery } from "@chakra-ui/react"
import { Fade } from "react-reveal"

import AboutMe from "./AboutMe"
import IsVisible from "./IsVisible"

import ImgAboutMe from "../assets/01233b05-5cd1-4256-be82-d6ef9e743b86.jpg"

function About() {
  const [isLargeThan850] = useMediaQuery("(min-width:850px)")

  const [isVisible, setIsVisible] = useState(false)

  return (
    <Box display="flex" justifyContent="space-around" pt="60px" h="100%">
      {isLargeThan850 && (
        <Box flex={1}>
          <Image
            src={ImgAboutMe}
            objectFit="contain"
            backgroundSize="cover"
            backgroundPosition="center"
            backgroundRepeat="no-repeat"
            h="100%"
            ml="auto"
            mr="auto"
            borderRadius="100%"
          />
        </Box>
      )}
      <IsVisible
        handleOnVisible={() => {
          setIsVisible(true)
        }}
      />
      <Box flex={1} fontFamily="Roboto">
        {isVisible && (
          <Fade bottom>
            <AboutMe />
          </Fade>
        )}
      </Box>
    </Box>
  )
}

export default About
