import { useState, useMemo } from "react"
import {
  Avatar,
  Stack,
  Box,
  Button,
  Spacer,
  useMediaQuery,
  Text,
} from "@chakra-ui/react"
import { GiHamburgerMenu } from "react-icons/gi"

import ScrollProgress from "./ScrollProgress"
import HeaderMenu from "./HeaderMenu"

import imageAvatar from "../assets/cbdf6106-2060-4f1a-b57e-6b87a67f5a5e.jpg"

function Header() {
  const [toggleMenu, setToggleMenu] = useState(false)

  const [isLargeThan850] = useMediaQuery("(min-width:850px)")

  useMemo(() => {
    if (isLargeThan850) {
      setToggleMenu(true)
    } else {
      setToggleMenu(false)
    }
  }, [isLargeThan850])

  return (
    <Box position="fixed" top="0" width="100%" zIndex="100">
      <ScrollProgress />
      <Stack
        display={isLargeThan850 && "flex"}
        flexDirection={isLargeThan850 && "row"}
        justifyContent={isLargeThan850 && "space-around"}
        alignItems="center"
        // background="teal.400"
        background="transparent"
        p={4}
        pt={0}
      >
        <Box display={!isLargeThan850 && "flex"} alignItems="center">
          <Box display="flex" alignItems="center">
            <Avatar src={imageAvatar} />
            <Text pl={3} fontFamily="Roboto">
              Mahdi Zoraghi
            </Text>
          </Box>
          {!isLargeThan850 && (
            <>
              <Spacer />
              <Button
                colorScheme="blue"
                width="15%"
                onClick={() => setToggleMenu(prevState => !prevState)}
              >
                <GiHamburgerMenu />
              </Button>
            </>
          )}
        </Box>
        <Spacer />
        {toggleMenu && <HeaderMenu isLargeThan850={isLargeThan850} />}
      </Stack>
    </Box>
  )
}

export default Header
