import { Box, Heading, Text } from "@chakra-ui/react"
import { useSelector } from "react-redux"
import Typed from "react-typed"

import SocialMediaList from "./SocialMediaList"

function Home() {
  const typeSkills = useSelector(state => state.typeSkills)

  return (
    <Box w="100%" h="100%">
      <Box
        position="absolute"
        top="50%"
        left="50%"
        transform="translate(-50%, -50%)"
        w="100%"
        textAlign="center"
        fontFamily="Roboto"
        pt="50px"
      >
        <Heading>
          <Typed
            // loop
            strings={[
              "Hi. I'm Mahdi Zoraghi",
              "Im Work at <span style='color:green'>Front End Developer</span>",
            ]}
            typeSpeed={70}
            backDelay={700}
            backSpeed={100}
          />
        </Heading>
        <Text pt="2" fontSize="20px">
          <Typed
            strings={typeSkills.map(
              skill =>
                `I have experience with <span style='color:green'>${skill}</span>`
            )}
            typeSpeed={80}
            startDelay={5500}
            loop
            backDelay={700}
            backSpeed={100}
          />
        </Text>
        <SocialMediaList />
      </Box>
    </Box>
  )
}

export default Home
