import { Box, IconButton } from "@chakra-ui/react"
import { motion } from "framer-motion"
import {
  ImGithub,
  ImInstagram,
  ImLinkedin,
  ImStackoverflow,
} from "react-icons/im"

function SocialMediaList() {
  return (
    <Box
      overflow="hidden"
      w="100%"
      pt="20px"
      pb="20px"
      display="flex"
      justifyContent="space-evenly"
    >
      <Box>
        <motion.div
          initial={{ scale: 0 }}
          animate={{ rotate: 360, scale: 1 }}
          transition={{
            type: "spring",
            stiffness: 260,
            damping: 40,
            delay: 6,
          }}
        >
          <IconButton
            transform="scale(1.5)"
            as="a"
            href="https://stackoverflow.com/users/14837115/mahdi-zoraghi"
          >
            <ImStackoverflow />
          </IconButton>
        </motion.div>
      </Box>
      <Box>
        <motion.div
          initial={{ scale: 0 }}
          animate={{ rotate: 360, scale: 1 }}
          transition={{
            type: "spring",
            stiffness: 260,
            damping: 40,
            delay: 6.25,
          }}
        >
          <IconButton
            transform="scale(1.5)"
            as="a"
            href="https://github.com/mahdi-zoraghi"
          >
            <ImGithub />
          </IconButton>
        </motion.div>
      </Box>
      <Box>
        <motion.div
          initial={{ scale: 0 }}
          animate={{ rotate: 360, scale: 1 }}
          transition={{
            type: "spring",
            stiffness: 260,
            damping: 40,
            delay: 6.5,
          }}
        >
          <IconButton
            transform="scale(1.5)"
            as="a"
            href="https://www.linkedin.com/in/mahdi-zoraghi-152a73157/"
          >
            <ImLinkedin />
          </IconButton>
        </motion.div>
      </Box>
      <Box>
        <motion.div
          initial={{ scale: 0 }}
          animate={{ rotate: 360, scale: 1 }}
          transition={{
            type: "spring",
            stiffness: 260,
            damping: 40,
            delay: 6.75,
          }}
        >
          <IconButton
            transform="scale(1.5)"
            as="a"
            href="https://www.instagram.com/mahdi_zoraghi/"
          >
            <ImInstagram />
          </IconButton>
        </motion.div>
      </Box>
    </Box>
  )
}

export default SocialMediaList
