import { useState } from "react"
import {
  Box,
  Button,
  Input,
  Stack,
  Textarea,
  FormControl,
  FormHelperText,
  Heading,
} from "@chakra-ui/react"
import emailjs from "emailjs-com"

function Contact() {
  const [data, setData] = useState({
    subject: "",
    name: "",
    email: "",
    message: "",
  })

  const [formError, setFormError] = useState({
    subject: false,
    name: false,
    email: false,
    message: false,
  })

  const sendEmail = e => {
    e.preventDefault()

    if (data.subject) {
      if (data.name) {
        if (data.email) {
          if (data.message) {
            emailjs
              .sendForm(
                "service_9gsa8gj",
                "template_xsp3ptg",
                e.target,
                "user_atHyxzXBlkuQ6UuWJMXpy"
              )
              .then(
                result => {
                  console.log("success: ", result.text)
                },
                error => {
                  console.log("error: ", error.text)
                }
              )
            setFormError({
              subject: false,
              name: false,
              email: false,
              message: false,
            })
            e.target.reset()
          } else {
            setFormError({ ...formError, message: true })
          }
        } else {
          setFormError({ ...formError, email: true })
        }
      } else {
        setFormError({ ...formError, name: true })
      }
    } else {
      setFormError({ ...formError, subject: true })
    }
  }

  return (
    <Box
      p="10px"
      pt="75px"
      w="100%"
      h="100%"
      position="absolute"
      top="33%"
      transform="translateY(-30%)"
      fontFamily="Roboto"
    >
      <Box ml="5%" pb="30px">
        <Heading
          w="200px"
          size="lg"
          fontFamily="Roboto"
          background="gray.300"
          color="black"
          textAlign="center"
          p="10px"
          borderRadius="8px"
        >
          Contact Me
        </Heading>
      </Box>
      <form
        style={{
          width: "80%",
          marginLeft: "auto",
          marginRight: "auto",
        }}
        onSubmit={sendEmail}
      >
        <Stack spacing={6}>
          <FormControl>
            <Input
              type="text"
              placeholder="Subject"
              name="subject"
              size="lg"
              onChange={e => setData({ ...data, subject: e.target.value })}
            />
            <FormHelperText hidden={!formError.subject} color="#fff">
              Please Set a Subject
            </FormHelperText>
          </FormControl>
          <FormControl>
            <Input
              type="text"
              name="name"
              size="lg"
              placeholder="Name"
              onChange={e => setData({ ...data, name: e.target.value })}
            />
            <FormHelperText hidden={!formError.name} color="#fff">
              Please Enter Your Name
            </FormHelperText>
          </FormControl>
          <FormControl>
            <Input
              type="email"
              name="email"
              size="lg"
              placeholder="Email"
              onChange={e => setData({ ...data, email: e.target.value })}
            />
            <FormHelperText hidden={!formError.email} color="#fff">
              Enter Your Email
            </FormHelperText>
          </FormControl>
          <FormControl>
            <Textarea
              placeholder="message..."
              name="message"
              size="lg"
              minHeight="150px"
              onChange={e => setData({ ...data, message: e.target.value })}
            />
            <FormHelperText hidden={!formError.message} color="#fff">
              Send Me a message
            </FormHelperText>
          </FormControl>
          <Box position="relative">
            <Button
              w="50%"
              size="lg"
              position="absolute"
              left="50%"
              transform="translateX(-50%)"
              type="submit"
              colorScheme="green"
            >
              Send
            </Button>
          </Box>
        </Stack>
      </form>
    </Box>
  )
}

export default Contact
