import { Box, Button, Heading, Text } from "@chakra-ui/react"
import { useSelector } from "react-redux"

function AboutMe() {
  const {
    aboutMe,
    contactDetails: { name, location, number, email, downloadResumeUrl },
  } = useSelector(state => state)

  return (
    <Box pt="20px" pl="20px">
      <Heading
        w="200px"
        size="lg"
        fontFamily="Roboto"
        background="gray.300"
        color="black"
        textAlign="center"
        p="10px"
        borderRadius="8px"
      >
        About Me
      </Heading>
      <Text p="15px" pt="20px" lineHeight="30px" fontFamily="Roboto">
        {aboutMe}
      </Text>
      <Box pt="20px">
        <Heading
          w="250px"
          size="lg"
          fontFamily="Roboto"
          background="gray.300"
          color="black"
          textAlign="center"
          p="10px"
          borderRadius="8px"
        >
          Contact Details
        </Heading>
        <Box
          display="flex"
          justifyContent="space-between"
          p="10px"
          w="70%"
          flexWrap="wrap"
        >
          <Box lineHeight="35px">
            <Text>{name}</Text>
            <Text>{location}</Text>
            <Text>{number}</Text>
            <Text>{email}</Text>
          </Box>
          <Box>
            <Button as="a" href={downloadResumeUrl}>
              Download Resume
            </Button>
          </Box>
        </Box>
      </Box>
    </Box>
  )
}

export default AboutMe
