import { Box } from "@chakra-ui/react"

import SkillItem from "./SkillItem"

import javascriptLogo from "../assets/icons/javascript-logo.jpg"
import reactLogo from "../assets/icons/react-logo.jpg"
import nextLogo from "../assets/icons/next-logo.jpg"
import reactNativeLogo from "../assets/icons/react-native-logo.jpg"
import typescriptLogo from "../assets/icons/typescript-logo.jpg"
import webpackLogo from "../assets/icons/webpack-logo.jpg"

/*
  colors:
    javascript : #f7df1d && rgba(247,223,29,255) && url : https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/480px-Unofficial_JavaScript_logo_2.svg.png
    typescript : #3178c6 && rgba(49,120,198,255) && url : https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS_16ycVDLnLy0nYPOuF6xx1NxUQ7UFZZN0yw&usqp=CAU&ec=45774421
    reactJS : #62dafd && rgba(98,218,252,255) || #1f1f1f && rgba(31,31,31,255) && url : https://miro.medium.com/max/500/1*cPh7ujRIfcHAy4kW2ADGOw.png
    webpack : #8cd7fb && rgba(142,214,252,255) && url : https://banner2.cleanpng.com/20190417/yb/kisspng-webpack-computer-icons-scalable-vector-graphics-re-webpack-svg-icon-transparent-amp-png-clipart-fre-5cb79870aa3cb3.6069044115555359846973.jpg
    NextJS : #000000 && rgba(0,0,0,255) || #ffffff && rgba(255,255,255,255) && url : https://decodenatura.com/static/fb8aa1bb70c9925ce1ae22dc2711b343/nextjs-logo.png 
    || https://seeklogo.com/images/N/next-js-logo-8FCFF51DD2-seeklogo.com.png
    React Native : #34d9fc && rgba(52,217,252,255) &&  url : https://miro.medium.com/max/1000/1*ub1DguhAtkCLvhUGuVGr6w.png
*/

function Skills() {
  return (
    <Box pt="60px" w="100%" h="100%">
      <SkillItem
        skillNumber={100}
        skill="Javascript"
        color="rgba(247,223,29,255)"
        logoUrl={javascriptLogo}
      />
      <SkillItem
        skillNumber={100}
        skill="ReactJS"
        color="rgba(98,218,252,255)"
        logoUrl={reactLogo}
      />
      <SkillItem
        skillNumber={100}
        skill="NextJS"
        color="rgba(0,0,0,255)"
        logoUrl={nextLogo}
      />
      <SkillItem
        skillNumber={80}
        skill="React Native"
        color="rgba(52,217,252,255)"
        logoUrl={reactNativeLogo}
      />
      <SkillItem
        skillNumber={80}
        skill="Typescript"
        color="rgba(49,120,198,255)"
        logoUrl={typescriptLogo}
      />
      <SkillItem
        skillNumber={80}
        skill="Webpack"
        color="rgba(142,214,252,255)"
        logoUrl={webpackLogo}
      />
    </Box>
  )
}

export default Skills
