import { useState } from "react"
import { Box, CircularProgress, Progress, Text, Image } from "@chakra-ui/react"
import { useCountUp } from "react-countup"

import IsVisible from "./IsVisible"

function SkillItem({ skillNumber, skill, color, logoUrl }) {
  const [isShow, setIsShow] = useState(false)

  const { countUp, start } = useCountUp({
    start: 0,
    end: skillNumber,
    duration: 3,
  })

  return (
    <Box
      ml="auto"
      mr="auto"
      mt="10px"
      w="90%"
      h="80px"
      background="blue.700"
      borderRadius="8px"
      p="5px"
      display="flex"
      alignItems="center"
      justifyContent="center"
    >
      <Box flex={0.2} display="flex" alignItems="center" pr={2}>
        <Box position="relative">
          <CircularProgress
            value={countUp}
            size="80px"
            color={color}
            thickness="4px"
            min={0}
            max={100}
          />
          <Image
            src={logoUrl}
            width="60px"
            height="60px"
            position="absolute"
            top="50%"
            left="50%"
            transform="translate(-50%, -50%)"
            borderRadius="50%"
            objectFit="contain"
            backgroundSize="cover"
            backgroundPosition="center"
            backgroundRepeat="no-repeat"
          />
        </Box>
        <Text fontFamily="Roboto" flexGrow={1} textAlign="center">
          {skill}
        </Text>
      </Box>
      <Box flex={0.9}>
        <Progress
          value={countUp}
          min={0}
          max={100}
          size="md"
          colorScheme="gray"
          borderRadius="8px"
        />
      </Box>
      {!isShow && (
        <IsVisible
          handleOnVisible={() => {
            setTimeout(
              () => {
                setIsShow(true)
              },
              // 0
              2000
            )
            setTimeout(() => {
              start()
            }, 500)
          }}
        />
      )}
    </Box>
  )
}

export default SkillItem
